  }
  
  .slide {
    height: 70vh;
    background-size: cover !important;
  }
  
  .head3 {
    left: 0;
    transform: rotate(180deg) translateY(calc(50% + 0px));
  }  
  
  .slider-content .inner h2 {
    /* font-weight: 900; */
    margin: 0 auto;
    /* max-width: 840px; */
    /* color: white; */
    color: #ccdaee;

    font-size: 44px;
    line-height: 1.5;
  }
  
  @media (max-height: 600px) {
    .slider-content .inner h2 {
      font-size: 1.8rem;
